<template>
    <div class="release-buy">
        <div class="buy-c">
            <div class="buy-form">
                <p class="buy-title">
                    {{$t('french.releaseBuy[0]')}}
                </p>
                <p class="buy-content">
                    {{$t('french.releaseBuy[1]')}}
                </p>
                <div class="input-c">
                    <div class="input-warp">
                        <p class="label">
                            {{$t('french.releaseBuy[2]')}}
                        </p>
                        <el-input class="input" v-model="form.price" :placeholder="priceData.price + 'CNY'">
                        </el-input>
                    </div>
                    <div class="input-warp">
                        <p class="label">
                            {{$t('french.releaseBuy[3]')}}
                        </p>
                        <el-input class="input" v-model="form.trans_num">
                        </el-input>
                    </div>
                    <div class="input-warp">
                        <p class="label">
                            {{$t('french.releaseBuy[4]')}}
                        </p>
                        <el-input class="input harf-input" :placeholder="$t('french.releaseBuy[5]')" v-model="form.max_price" @blur="changeMaxPrice">
                        </el-input>
                        <el-input class="input harf-input" :placeholder="$t('french.releaseBuy[6]')" v-model="form.min_price" @blur="changeMinPrice">
                        </el-input>
                        <span class="unit-height unit">
                            CNY
                        </span>
                        <span class="unit-down unit">
                            CNY
                        </span>
                    </div>
                    <div class="input-warp">
                        <p class="label">
                            {{$t('french.releaseBuy[7]')}}
                        </p>
                        <el-input class="input" v-model="form.notes">
                        </el-input>
                    </div>
                    <p class="pay-title">
                        {{$t('french.releaseBuy[8]')}}
                    </p>
                    <template>
                        <el-checkbox-group 
                            v-model="checkedList">
                            <div v-for="(item,i) in paymentList" :key="i">
                                <el-checkbox  :label="item.tip" v-if="item.status == 1">
                                    <p class="pay-warp">
                                        <img :src="getImg(item.type)" alt="" class="pay-img"/>
                                        <span class="bank-desc" v-if="item.tip">
                                            {{ item.tip }}
                                        </span>
                                        <span class="bank-desc" v-if="item.number">
                                            {{ item.number }}
                                        </span>
                                        <span class="bank-desc" v-if="item.bank">
                                            {{ item.bank }}
                                        </span>
                                        <span class="bank-desc" v-if="item.branch">
                                            {{ item.branch }}
                                        </span>
                                    </p>
                                </el-checkbox>
                            </div>
                        </el-checkbox-group>
                    </template>
                    <el-button class="buy-button" type="primary" @click="handleBuy">
                        {{$t('french.releaseBuy[9]')}}
                    </el-button>
                </div>
            </div>
            <div class="buy-tabel">
                <p class="title clr">
                    <span class="buy-name">
                        {{$t('french.releaseBuy[10]')}}
                    </span>
                    <span class="show-more" @click="showMore">
                        {{$t('french.releaseBuy[11]')}}
                    </span>
                </p>
                <template>
                    <el-table :data="tableData" style="width: 100%">
                        <el-table-column prop="order_no" :label="$t('french.releaseBuy[12]')" width="180" align="center">
                        </el-table-column>
                        <el-table-column :label="$t('french.releaseBuy[13]')" width="180">
                            <template slot-scope="scope">
                                <span>
                                    {{ scope.row.trans_num }}
                                </span>
                                <span>
                                    USDT
                                </span>
                            </template>
                        </el-table-column>
                        <el-table-column :label="$t('french.releaseBuy[14]')">
                            <template slot-scope="scope">
                                <span>
                                    {{ scope.row.deals_num }}
                                </span>
                                <span>
                                    USDT
                                </span>
                            </template>
                        </el-table-column>
                        <el-table-column :label="$t('french.releaseBuy[15]')">
                            <template slot-scope="scope">
                                <span>
                                    {{ scope.row.totalprice }}
                                </span>
                                <span>
                                    CNY
                                </span>
                            </template>
                        </el-table-column>
                        <el-table-column :label="$t('french.releaseBuy[16]')">
                            <template slot-scope="scope">
                                <span>
                                    {{ scope.row.price }}
                                </span>
                                <span>
                                    CNY
                                </span>
                            </template>
                        </el-table-column>
                        <el-table-column :label="$t('french.releaseBuy[17]')">
                            <template slot-scope="scope">
                                {{ $public.timestampToTime(scope.row.add_time) }}
                            </template>
                        </el-table-column>
                        <el-table-column :label="$t('french.releaseBuy[18]')" align="center">
                            <template slot-scope="scope">
                                <el-button type="primary" size="mini" @click="undo(scope.row.id)" v-if="scope.row.status == 1">
                                    {{$t('french.releaseBuy[19]')}}
                                </el-button>
                                <span v-if="scope.row.status == 2" style="color: green">
                                    {{$t('french.releaseBuy[20]')}}
                                </span>
                                <span v-if="scope.row.status == 3" style="color: red">
                                    {{$t('french.releaseBuy[21]')}}
                                </span>
                            </template>
                        </el-table-column>
                    </el-table>
                </template>
            </div>
            <!-- 确认下单弹窗 -->
            <el-dialog :title="$t('french.releaseBuy[22]')" :visible.sync="dialogVisible">
                <p class="dialog-detail clr">
                    <span class="dialog-name">
                        {{$t('french.releaseBuy[23]')}}
                    </span>
                    <span class="dialog-desc">
                        {{ form.price }}
                    </span>
                    <span class="unit">
                        CNY
                    </span>
                </p>
                <p class="dialog-detail clr">
                    <span class="dialog-name">
                        {{$t('french.releaseBuy[24]')}}
                    </span>
                    <span class="dialog-desc">
                        {{ form.price * form.trans_num }}
                    </span>
                    <span class="unit">
                        CNY
                    </span>
                </p>
                <p class="dialog-detail clr">
                    <span class="dialog-name">
                        {{$t('french.releaseBuy[25]')}}
                    </span>
                    <span class="dialog-desc">
                        {{ form.trans_num }}
                    </span>
                    <span class="unit">
                        USDT
                    </span>
                </p>
                <el-input type="password" :placeholder="$t('french.releaseBuy[26]')" v-model="form.tpwd">
                </el-input>
                <div class="btn-group">
                    <el-button type="primary" @click="submitOrder" class="dialog-btn">
                        {{$t('french.releaseBuy[27]')}}
                    </el-button>
                    <el-button @click="dialogVisible = false" class="dialog-btn">
                        {{$t('french.releaseBuy[28]')}}
                    </el-button>
                </div>
            </el-dialog>
            <!-- 撤单弹窗 -->
            <el-dialog :title="$t('french.releaseBuy[29]')" :visible.sync="backDialogVisible">
                <span>
                    {{$t('french.releaseBuy[30]')}}
                </span>
                <div class="btn-group">
                    <el-button type="primary" @click="submitUndo" class="dialog-btn">
                        {{$t('french.releaseBuy[31]')}}
                    </el-button>
                    <el-button @click="backDialogVisible = false" class="dialog-btn">
                        {{$t('french.releaseBuy[32]')}}
                    </el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import { paymentList, pmma, pmmaLists, pmmaRev, getPrice } from '@/api/c2c.js';
export default {
  name: 'releaseBuy',
  data() {
    return {
        checkedList: [],
        form: {
            price: '',
            trans_num: '',
            max_price: '',
            min_price: '',
            tpwd: '',
            notes: '',
            type: 'pmma',
            pay_wx: 0,
            pay_alipay: 0,
            pay_backcard: 0
        },
        backDialogVisible: false,
        dialogVisible: false,
        tableData: [],
        paymentList: [],
        undoId: '',
        priceData: {}
    }
  },
  created() {
    //   获取初始列表数据
      this.getDataList()
  },
  methods: {
        getImg(type) {
            if (type == 'backcard') {
                return require("../../../assets/img/frenchImg/bank_small.png")
            } else if (type == 'wx') {
                return require("../../../assets/img/frenchImg/wechat_small.png")
            } else if (type="alipay") {
                return require("../../../assets/img/frenchImg/alipay_small.png")
            }
        },
        //   获取初始列表数据
        getDataList() {
            //   获取当前登录账户求购列表
            pmmaLists().then(res=> {
                if (res.data.status == 200) {
                    this.tableData = res.data.data.res
                } else {
                    this.$public.msg(res.data.msg,'error',this)
                    return
                }
            })
            //   获取当前登录账户的支付方式
            paymentList().then(res=> {
                if (res.status == 200) {
                    this.paymentList = res.data.data
                }
            })
            // 获取参考价CNY
            getPrice().then(res=> {
                if (res.data.status == 200) {
                    this.priceData = res.data.data
                    return
                }
            })
        },
        // 最大限额
        changeMaxPrice() {
            if (this.form.min_price == '') {
                return
            } else {
                if (Number(this.form.max_price) < Number(this.form.min_price)) {
                    this.$public.msg(this.$t('french.releaseBuy[33]'), 'error', this)
                    this.form.max_price = ''
                    return
                }
            }
        },
        // 最小限额
        changeMinPrice() {
            if (this.form.max_price == '') {
                return
            } else {
                if (Number(this.form.max_price) < Number(this.form.min_price)) {
                    this.$public.msg(this.$t('french.releaseBuy[34]'), 'error', this)
                    this.form.min_price = ''
                    return
                }
            }
        },
        // 撤单
        undo(id) {
            this.undoId = id
            this.backDialogVisible = true
        },
        // 确认撤单
        submitUndo() {
            pmmaRev({ type: 'pmma', id: this.undoId }).then(res=> {
                if (res.data.status == 200) {
                    this.$public.msg(this.$t('french.releaseBuy[35]'), 'success', this)
                    this.getDataList()
                    this.backDialogVisible = false
                } else {
                    this.$public.msg(res.data.msg, 'error', this)
                    this.backDialogVisible = false
                }
            })
        },
        // 确认发布购买
        handleBuy() {
            if (!sessionStorage.account || !sessionStorage.token) {
                this.$public.msg(this.$t('french.releaseBuy[36]'), 'error' ,this)
                this.$router.push({ name: 'login' })
            } else {
                if (this.form.price == '') {
                    this.$public.msg(this.$t('french.releaseBuy[37]'),'error',this)
                    return
                } else if (this.form.trans_num == '') {
                    this.$public.msg(this.$t('french.releaseBuy[38]'),'error',this)
                    return
                } else if (this.form.max_price == '') {
                    this.$public.msg(this.$t('french.releaseBuy[39]'),'error',this)
                    return
                } else if (this.form.min_price == '') {
                    this.$public.msg(this.$t('french.releaseBuy[40]'),'error',this)
                    return
                } else if (this.checkedList == []) {
                    this.$public.msg(this.$t('french.releaseBuy[41]'),'error',this)
                    return
                } else {
                    this.dialogVisible = true
                }
                this.form.tpwd = ''
                this.dialogVisible = true
            }
        },
        // 确认下单
        submitOrder() {
            if (this.form.tpwd == '') {
                this.$public.msg(this.$t('french.releaseBuy[42]'),'error',this)
                return
            } else if (!this.$public.pwd(this.form.tpwd)) {
                this.$public.msg(this.$t('french.releaseBuy[43]'),'error',this)
                this.form.tpwd = ''
                return
            } else {
                // google
                this.$public.googleVerify().then((r)=> {
                    // 资金密码加密
                    this.form.tpwd = this.$public.Cryptographiccapital(this.form.tpwd)
                    if(this.checkedList.indexOf('银行卡') > -1){
                        this.form.pay_backcard = 1
                    }
                    if(this.checkedList.indexOf('微信') > -1){
                        this.form.pay_wx = 1
                    }
                    if(this.checkedList.indexOf('支付宝') > -1){
                        this.form.pay_alipay = 1
                    }
                    var postData = JSON.parse(JSON.stringify(this.form))
                    pmma(postData).then(res=> {
                        if (res.data.status == 200) {
                            this.$public.msg(this.$t('french.releaseBuy[44]'),'success',this)
                            this.dialogVisible = false
                            this.form.price = ''
                            this.form.trans_num = ''
                            this.form.min_price = ''
                            this.form.max_price = ''
                            this.form.notes = ''
                            this.getDataList()
                        } else {
                            this.$public.msg(res.data.msg,'error',this)
                            this.dialogVisible = false
                            return
                        }
                    })
                }).catch((r)=>{})
            }
        },
        // 查看更多
        showMore() {
            this.$router.push({ name: 'transactionDetail' })
        } 
    },
    watch: {
        'form.price'(n, o) {
            var de = String(n).split(".")
            // 非数字限制
            if (isNaN(Number(n))) {
                this.form.price = o
            }
            // 位数限制 位数限制 限价时
            if (de.length == 2) {
                if (de[1].length > 2) {
                    this.form.price = o
                }
            }
        },
        'form.trans_num'(n, o) {
            var de = String(n).split(".")
            // 非数字限制
            if (isNaN(Number(n))) {
                this.form.trans_num = o
            }
            // 位数限制 位数限制 限价时
            if (de.length == 2) {
                if (de[1].length > 4) {
                    this.form.trans_num = o
                }
            }
        },
        'form.max_price'(n, o) {
            var de = String(n).split(".")
            // 非数字限制
            if (isNaN(Number(n))) {
                this.form.max_price = o
            }
            // 位数限制 位数限制 限价时
            if (de.length == 2) {
                if (de[1].length > 2) {
                    this.form.max_price = o
                }
            }
        },
        'form.min_price'(n, o) {
            var de = String(n).split(".")
            // 非数字限制
            if (isNaN(Number(n))) {
                this.form.min_price = o
            }
            // 位数限制 位数限制 限价时
            if (de.length == 2) {
                if (de[1].length > 2) {
                    this.form.min_price = o
                }
            }
        }
    }
}
</script>
<style lang="less">
   @import 'releaseBuy.less';
</style>



